import React from 'react';
import CheckBoxFilters from '../../../../Views/CatalogViews/CheckBoxFilters';

const CheckBoxFiltersContainer = ({
  role = { number: 2 },
  valueCheckBoxFilters,
  loadData,
}) => {

  const handleChangeFilters = (e) => {
    const value = e.checked;
    const key = e.name
    if (e.checked === null) return;
    let data = {
      ...valueCheckBoxFilters,
      [key]: !value
    }
    loadData(data)
  };

   
  return (
    <CheckBoxFilters
      valueCheckBoxFilters={valueCheckBoxFilters}
      handleChangeFilters={handleChangeFilters}
      role={role}
    />
  );
};

export default (CheckBoxFiltersContainer);
