import React, { memo } from "react";
import AsyncComponent from '../../../helpers/asyncComponent';
import { Formik } from "formik";
import Text from "../../../helpers/Text";
import Offset from "../../../Views/Offset";
import Block from '../../../Views/GridContainerBlock';
import Container from '../../../Views/GridContainerBlock/Container';
import Breadcrumbs from '../../../Views/Breadcrumbs';
import EmptyCatalog from '../../../Views/CatalogViews/EmptyCatalog';
import SubmitButton from '../../../Views/CatalogViews/SubmitButton';
import Wrapper from '../../../Views/CatalogViews/Wrapper';
import Filters from '../../../Views/CatalogViews/Filters';
import SortSelect from '../../../Views/CatalogViews/SortSelect';
import Tags from '../../../Views/CatalogViews/Tags';
import WrapperCard from '../../../Views/CatalogViews/WrapperCard';
import Catalog from '../../../Views/CatalogViews/Catalog';
import Row from '../../../Views/CatalogViews/Row';
import Title from "../../../Views/Title";
import Button from "../../../Views/Button";
import { closeJustIcon } from '../../../images';
import Form from '../../../Views/Form';
import CheckBoxFiltersContainer from './CatalogFilters/CheckBoxFiltersContainer';
import WarningBlock from '../../../Views/WarningBlock';
import BlockSpinner from '../../../Views/SpinnerWrapper';
import Spinner from '../../../Views/SpinnerWrapper/Spinner';
import SpinnerWrapperSpinnerCenterMargin from '../../../Views/SpinnerWrapper/SpinnerWrapperSpinnerCenterMargin';
import EnabledFilters from './CatalogFilters/EnabledFilters';
import Pagination from '../../../Views/Pagination';
import { ROLE } from "../../../const";

const AsynNoProducts = AsyncComponent(() => {
  return import('../../../Views/ProductDetailsViews/InfoBlockProducts');
});
const EnabledFiltersOptions = AsyncComponent(() => {
  return import('./CatalogFilters/EnabledFiltersOptions');
});
  
const AsynColorsFilters = AsyncComponent(() => {
  return import('./CatalogFilters/ColorsFilters');
});

const AsynSizesFilters = AsyncComponent(() => {
  return import('./CatalogFilters/SizesFilters');
});

const AsyncBrandsFilters = AsyncComponent(() => { // сдесь бъёт ошибку
  return import('./CatalogFilters/BrandsFilters');
});

const AsyncTypeProductFilters = AsyncComponent(() => {
  return import('./CatalogFilters/TypeProductFilters');
});

const AsyncProductCard = AsyncComponent(() => {
  return import('../../../Views/ProductCard');
});

const AsyncYouHaveAlreadyWatched = AsyncComponent(() => {
  return import('../../../Views/YouHaveAlreadyWatched/YouHaveAlreadyWatchedContainer');
});

const CatalogPageLayout = ({
  isLoadingMyWishYouAlredy,
  isLoadingMyWishRecomended,
  breadcrumbs,
  setShowFilters,
  showFilters,
  isShowBtnSubmit,
  offsetTopBtnSubmit,
  youAlredyWatch,
  recommendedProducts,
  isLoadingMyWish,
  isLoading,
  content,
  dataProducts,
  valueProducts,
  valueCheckBoxFilters,
  filterParams,
  currentPage,
  options,
  currency,
  isMobile,
  role,
  isFilters,
  loadData,
  getTitleForDocument,
  openBtnSubmit,
  resetAllFilters,
  resetContextFilter,
  checkIsShowCategorysAndProducType,
  handlerChangePaginations,
  showMore,
  showMoreSpinner,
  offsetLeftBtnSubmit,
}) => {
  return (
    <React.Fragment>
      <Container>
        <Breadcrumbs breadcrumbs={breadcrumbs} />
        <React.Fragment>
          <Row>
            <Catalog>
              <Title variant={'catalog-heading'} type={'h1'}>
                {
                  getTitleForDocument(valueProducts)
                }
              </Title>
            </Catalog>
          </Row>
          <Row>
            <React.Fragment>
              <Filters>
                <Button
                  full
                  onClick={() => {
                    setShowFilters(!showFilters)
                  }
                  }
                  onClickIcon={() => {
                    setShowFilters(false)
                  }
                  }
                  classNameIcon={'catalog_mobile__icon'}
                  variant={
                    !showFilters ? 'catalog_mobile__filter' : 'catalog_mobile__filter-closed'
                  }
                  iconRight={showFilters && closeJustIcon}
                >
                  <Text text={'filters'} />
                </Button>

                <Wrapper
                  isShowMobileFilters={showFilters}
                  catfilter={true}
                >
                  <Formik
                    enableReinitialize
                    initialValues={filterParams}
                    onSubmit={(data) => { loadData(data) }}
                  >
                    {({ handleSubmit, values, setValues }) => {

                      return (
                        <Form novalidate onClick={handleSubmit}>
                          <SubmitButton
                            isShowBtnSubmit={isShowBtnSubmit}
                            offsetLeftBtnSubmit={offsetLeftBtnSubmit}
                            offsetTop={offsetTopBtnSubmit}
                            onClick={() => handleSubmit(values)}
                          />

                          <CheckBoxFiltersContainer
                            valueCheckBoxFilters={valueCheckBoxFilters}
                            loadData={loadData}
                            role={role}
                          />

                          {

                            checkIsShowCategorysAndProducType() ? (
                              <AsyncTypeProductFilters
                                categories={valueProducts.by_type}
                                values={values}
                                handleSubmit={handleSubmit}
                                setValues={setValues}
                                openBtnSubmit={openBtnSubmit}
                              />
                            ) : null

                          }

                          {
                            role !== ROLE.RETAIL && role !== ROLE.UNREGISTRED ?
                              <AsyncBrandsFilters
                                brands={valueProducts.by_brand}
                                values={values}
                                openBtnSubmit={openBtnSubmit}
                                setValues={setValues}
                                role={role}
                              />
                              : null
                          } 

                          <AsynColorsFilters
                            colors={valueProducts.by_color}
                            values={values}
                            setValues={setValues}
                            openBtnSubmit={openBtnSubmit}
                          />

                          <AsynSizesFilters
                            sizes={valueProducts.by_size}
                            values={values}
                            setValues={setValues}
                            openBtnSubmit={openBtnSubmit}
                          />

                          <Wrapper mobBtns={true}>
                            <Button
                              onClick={resetAllFilters}
                              variant={'catalog_mobile__clear'}
                            >
                              <Text text={'reset.all'} />
                            </Button>
                            <Button
                              onClick={(e) => {
                                e.preventDefault();
                                handleSubmit();
                                // setShowFilters(false)

                              }}
                              variant={'catalog_mobile__apply'}
                            >
                              <Text text={'apply'} />
                            </Button>
                          </Wrapper>
                        </Form>
                      );
                    }}
                  </Formik>
                </Wrapper>
              </Filters>

              <Catalog>
                {
                  content? 
                  <WarningBlock
                    variant={'catalog-wrapper'}
                    textWarning={content ? <span dangerouslySetInnerHTML={{ __html: content }}></span> : ''}
                  />
                  : null
                }
                {
                  isLoading ?
                    <>
                      <SortSelect
                        valueOptionsSort={filterParams.ordering}
                        selectedSortFilters={(selected) => {
                          return loadData({ ordering: selected });
                        }}
                        options={options}
                      />

                      {
                        !valueProducts.length ?
                        <Tags>

                          <EnabledFiltersOptions
                            enabledFilterData={filterParams}
                            defaultFilterData={options}
                            translateKey={'options'}
                            resetContextFilter={resetContextFilter}
                            keyFilter={'ordering'}
                          />
                          <EnabledFilters
                            enabledFilterData={filterParams}
                            defaultFilterData={valueProducts}
                            resetContextFilter={resetContextFilter}
                            translateItem={'inStock'}
                            translateKey={'catalog'}
                            keyFilter={'is_in_stock'}
                          />
                          <EnabledFilters
                            enabledFilterData={filterParams}
                            defaultFilterData={valueProducts}
                            resetContextFilter={resetContextFilter}
                            translateItem={'new'}
                            translateKey={'catalog'}
                            keyFilter={'is_new'}
                          />
                          <EnabledFilters
                            enabledFilterData={filterParams}
                            defaultFilterData={valueProducts}
                            resetContextFilter={resetContextFilter}
                            translateItem={'hit'}
                            translateKey={'catalog'}
                            keyFilter={'is_bestseller'}
                          />
                          <EnabledFilters
                            enabledFilterData={filterParams}
                            defaultFilterData={valueProducts}
                            resetContextFilter={resetContextFilter}
                            translateItem={'sell.out'}
                            translateKey={'catalog'}
                            keyFilter={'is_closeout'}
                          />
                          <EnabledFilters
                            enabledFilterData={filterParams}
                            defaultFilterData={valueProducts}
                            translateKey={'category'}
                            resetContextFilter={resetContextFilter}
                            keyFilter={'categories'}
                          />

                          <EnabledFilters
                            enabledFilterData={filterParams}
                            defaultFilterData={valueProducts}
                            translateKey={'is_polish'}
                            resetContextFilter={resetContextFilter}
                            keyFilter={'is_import'}
                          />
                          <EnabledFilters
                            enabledFilterData={filterParams}
                            defaultFilterData={valueProducts}
                            translateKey={'is_import'}
                            resetContextFilter={resetContextFilter}
                            keyFilter={'is_polish'}
                          />
                          <EnabledFilters
                            enabledFilterData={filterParams}
                            defaultFilterData={valueProducts}
                            translateKey={'assembled'}
                            resetContextFilter={resetContextFilter}
                            keyFilter={'is_in_collection'}
                          />
                          <EnabledFilters
                            enabledFilterData={filterParams}
                            defaultFilterData={valueProducts}
                            translateKey={'not.in.rows'}
                            resetContextFilter={resetContextFilter}
                            keyFilter={'is_not_range'}
                          />

                          <EnabledFilters
                            enabledFilterData={filterParams}
                            defaultFilterData={valueProducts}
                            translateKey={'brand'}
                            resetContextFilter={resetContextFilter}
                            keyFilter={'brands'}
                          />
                          <EnabledFilters
                            enabledFilterData={filterParams}
                            defaultFilterData={valueProducts}
                            translateKey={'color'}
                            resetContextFilter={resetContextFilter}
                            keyFilter={'colors'}
                          />
                          <EnabledFilters
                            enabledFilterData={filterParams}
                            defaultFilterData={valueProducts}
                            translateKey={'size'}
                            resetContextFilter={resetContextFilter}
                            keyFilter={'sizes'}
                          />
                          {isFilters(filterParams, resetAllFilters)}
                        </Tags>
                        : null
                      }
                      {
                        !!!dataProducts?.results.length && showFilters ? <EmptyCatalog /> : null
                      }
                      {
                        !!dataProducts?.results.length ?
                          <WrapperCard>
                            {
                              dataProducts.results.map((el) => {

                                return (
                                  <AsyncProductCard
                                    role={role}
                                    key={`${el.title}-${el.id}`}
                                    title={el.title}
                                    id={el.id}
                                    url={el.url}
                                    brand={el.brand}
                                    prices={el.prices}
                                    stock={el.stock}
                                    colors={el.colors}
                                    sizes={el.sizes}
                                    images={el.images}
                                    isSales={el.isSales}
                                    isNew={el.isNew}
                                    isHit={el.isHit}
                                    is_liked={el.is_liked}
                                    product_rc={el.product_rc}
                                    article={el.article}
                                    isLoadingMyWish={isLoadingMyWish}
                                    currency={currency}
                                  />
                                );
                              })
                            }
                            {
                              showMoreSpinner ?
                                <SpinnerWrapperSpinnerCenterMargin>
                                  <Spinner sizeHeight="30" sizeWidth="30" />
                                </SpinnerWrapperSpinnerCenterMargin>
                                : null
                            }
                          </WrapperCard>
                          : <AsynNoProducts>
                            ПО ВАШЕМУ ЗАПРОСУ НИЧЕГО НЕ НАЙДЕНО.
                          </AsynNoProducts> 
                      }
                      {
                        dataProducts?.results.length < dataProducts?.count && currentPage * 30 < dataProducts?.count ? (
                          <Button full onClick={showMore} variant={'show_more'}>
                            <Text text={'show.more'} />
                          </Button>
                        ) : null
                      }

                      <Pagination
                        location={'left'}
                        count={isMobile? 10 : 30}
                        allCount={dataProducts?.count}
                        currentPage={currentPage ?? 1}
                        handlerChangePaginations={handlerChangePaginations}
                      />
                    </>
                    : <SpinnerWrapperSpinnerCenterMargin>
                      <Spinner sizeHeight="30" sizeWidth="30" />
                    </SpinnerWrapperSpinnerCenterMargin>
                }
              </Catalog>
            </React.Fragment>
          </Row>
        </React.Fragment>
      </Container>
      <AsyncYouHaveAlreadyWatched
        headerText={Text({ text: 'already.watched' })}
        youAlredyWatch={youAlredyWatch}
        isLoadingMyWish={isLoadingMyWishYouAlredy}
        currency={currency}
        role={role}
      />
      <AsyncYouHaveAlreadyWatched
        youAlredyWatch={recommendedProducts}
        isLoadingMyWish={isLoadingMyWishRecomended}
        headerText={'Рекомендуемое'}
        currency={currency}
        role={role}
      />
      <Offset offset={'catalog'} />
    </React.Fragment>
  )
}

export default CatalogPageLayout;